@mixin font-style($size, $margin, $line-height: 1) {
  margin-bottom: $margin;
  font-size: $size;
  line-height: $line-height;
}

.stepper {
  display: flex;
  margin-bottom: 0.25rem;

  &__container {
    margin: 1rem 0 0 1.5rem;
  }

  &__text-container {
    margin: -0.25rem 0 1rem;

    b {
      @include font-style(0.9rem, 1rem, 0.5rem);
    }

    p {
      @include font-style(0.75rem, 0.15rem);
      color: var(--desc-color);
    }

    a {
      @include font-style(1rem, 0.5rem, 1rem);
    }
  }

  &__line {
    width: 4px;
    background-color: var(--stepper-color);
    height: 100%;

    &.mobile {
      width: 2px;
    }
  }

  &__pointer {
    align-items: center;
    padding-right: 1.5rem;
  }

  &__circle {
    color: #fff;
    background: var(--stepper-color);
    margin-bottom: 0.25rem;
    height: 2rem;
    line-height: 2rem;
    aspect-ratio: 1/1;
    position: relative;
    font-size: 0.75rem;
    border-radius: 50%;

    &.desktop {
      aspect-ratio: 3/1;
      border-radius: 0;

      &:before {
        content: "";
        position: absolute;
        left: -1rem;
        top: 0;
        border-top: 1rem solid transparent;
        border-bottom: 1rem solid transparent;
        border-right: 1rem solid var(--stepper-color);
      }
    }
  }
}